<template>
	<div v-loading="loading" element-loading-text="">
		<div class="handle-box">
			<div class="btn">
				<el-button type="primary" @click="addDocument"></el-button>
			</div>
		</div>

		<el-dialog :visible.sync="dialogDocument" title="">
			<div class="box">
				<div style="margin: 0 10px">
					<h3 style="height: 40px; line-height: 40px;"></h3>
					<input type="text" v-model="form.title" style="width: 100%; height: 30px; line-height: 30px;font-size: 15px;" />
					<h3 style="height: 40px; line-height: 40px;"></h3>
					<quill-editor ref="myQuillEditor" v-model="form.detail" :options="editorOption" style="height:430px;margin:0 0 60px 0;"></quill-editor>
					<el-upload class="avatar-uploader quill-img" action="https://api.fengniaotuangou.cn/api/upload" :before-upload="beforeUpload"
					 :on-success="quillImgSuccess" style="display: none">
						<el-button size="small" type="primary" id="imgInput" element-loading-text=","></el-button>
					</el-upload>
					<div class="submit">
						<el-button type="primary" @click="newDocument"></el-button>
					</div>
				</div>
			</div>
		</el-dialog>

		<el-table :data="tableDate" border :header-cell-style="{background:'#f0f0f0'}" max-height="620">
			<!-- <el-table-column label="" type="selection"></el-table-column> -->
			<el-table-column prop="id" label="ID"></el-table-column>
			<el-table-column prop="title" label=""></el-table-column>
			<el-table-column prop="updated_at" label=""></el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-dialog :visible.sync="dialogDel" title="" width="20%" align="center" :close-on-click-modal="false">
			<div style="font-size: 20px; margin-bottom: 30px;"></div>
			<span>
				<el-button type="primary" @click="toDel"></el-button>
				<el-button type="danger" @click="dialogDel = false"></el-button>
			</span>
		</el-dialog>
		<div class="block">
			<el-pagination @current-change="currentChange" :current-page.sync="current" :page-sizes="[10, 20, 30, 40, 50]"
			 :page-size="size" layout="sizes, prev, pager, next, jumper" :total="total" @size-change="sizeChange"></el-pagination>
		</div>
	</div>
</template>

<script>
	import API from "@/api/index.js";
	import {
		quillEditor
	} from "vue-quill-editor";
	import "quill/dist/quill.core.css";
	import "quill/dist/quill.snow.css";
	import "quill/dist/quill.bubble.css";

	let loading;
	export default {
		name: "document",
		components: {
			quillEditor,
		},
		data() {
			return {
				loading: true,
				dialogDocument: false,
				form: {
					title: "",
					detail: "",
				},
				editorOption: {
					placeholder: "",
					theme: "snow",
					modules: {
						toolbar: {
							container: [
								["bold", "italic", "underline", "strike"],
								["blockquote", "code-block"],
								[{
									direction: "rtl",
								}, ],
								[{
									size: ["small", false, "large", "huge"],
								}, ],
								[{
									header: [1, 2, 3, 4, 5, 6, false],
								}, ],
								[{
										color: [],
									},
									{
										background: [],
									},
								],
								[{
									font: [],
								}, ],
								[{
									align: [],
								}, ],
								["clean"],
								["link", "image"],
							],
							handlers: {
								image: function(value) {
									if (value) {
										document.querySelector(".quill-img input").click();
									} else {
										this.quill.format("image", false);
									}
								},
							},
						},
					},
				},

				tableDate: [],
				dialogDel: false,
				id: "",
				current: 1,
				size: 10,
				total: 0,
			};
		},
		mounted() {
			this.getDocument();
		},
		methods: {
			// 
			getDocument() {
				var self = this;
				API.informations(self.current)
					.then((res) => {
						self.loading = false;
						self.tableDate = res.data;
						self.total = res.total;
					})
					.catch((err) => {
						self.loading = false;
					});
			},
			// 
			currentChange(val) {
				var self = this;
				self.current = val;
				self.loading = true;
				API.informations(val, self.size)
					.then((res) => {
						self.loading = false;
						self.tableDate = res.data;
						self.total = res.total;
					})
					.catch((err) => {
						self.loading = false;
					});
			},
			// 
			sizeChange(val) {
				var self = this;
				self.size = val;
				self.loading = true;
				API.informations(self.current, val).then((res) => {
					self.loading = false;
					self.tableDate = res.data;
					self.total = res.total;
				});
			},
			// 
			newDocument() {
				var self = this;
				API.information(self.form).then((res) => {
					self.$message.success("");
					self.dialogDocument = false;
					self.getDocument();
					self.form = {};
					self.current = 1;
				});
			},
			addDocument() {
				var self = this;
				self.dialogDocument = true;
				self.form = {
					title: "",
					detail: "",
				};
			},

			// 
			beforeUpload() {
				// 
				loading = this.$loading({
					lock: true,
					text: "",
					spinner: "el-icon-loading",
					background: "rgba(0, 0, 0, 0.7)",
				});
			},
			// 
			quillImgSuccess(res, file) {
				let quill = this.$refs.myQuillEditor.quill;
				// 
				loading.close();
				if (res.data) {
					let length = quill.getSelection().index;
					quill.insertEmbed(length, "image", res.data);
					quill.setSelection(length + 1);
				} else {
					this.$message.error("");
				}
			},

			handleEdit(index, row) {
				var self = this;
				self.dialogDocument = true;
				self.form = row;
			},
			// 
			handleDelete(index, row) {
				var self = this;
				self.dialogDel = true;
				self.id = row.id;
			},

			toDel() {
				var self = this;
				API.delDocument(self.id).then((res) => {
					self.$message.success("");
					self.dialogDel = false;
					self.getDocument();
					self.current = 1;
				});
			},
		},
	};
</script>

<style>
</style>
